.category{
   
    height: 130px;
    width: 130px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); 
    margin:3vw;
    border-radius: 5px;
   text-align: center;
    
}
.category img{
    margin-top:10px;
    width: 58%;
}
.category p{
    /* margin-top:10px; */
    text-align: left;
    margin-top:8px;
    padding-left:25px;
    padding-right: 25px;
    font-weight: 500;
    font-size:13px;
    color: #3C9C23;
    line-height: 1.2;
}
#categories .row {
    align-items: center;
    justify-content: center;
}
#categories .link{
    color:#3C9C23; 
    text-decoration: none;
}

 #navbartopcat p{
    text-align: center;
    font-family: 'Rubik', sans-serif;
    font-size: 20px; 
    margin-top:5px;
    margin-left: 30vw;
    
 }

#navbartopcat .row{
    height: 35px;
    margin-top:4px;
  
   
    
} 
#navbartopcat .arrowb{
    margin-top:15px;
    margin-left: 5px;
    width: 14px;
    height:14px; 
}
#icon {
    margin-top:16px;
    margin-bottom:16px;
}
.ButtonFavorite{
    margin-top: 10px;
    font-size:13px;
    width:285px;
    height: 30px;
    border-radius: 5px; 
    border-color: inherit;
    border-style: inherit;
    /* box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);  */
    color: white;
    font-weight: 500;
    background:#3C9C23;
    margin-bottom:10px;
   
}
 
 .Item{
    color: #4f4f4f;
    width: 145px;
    height: 205px; 
    margin: 2vw;
    border-radius: 5px;
    padding:0 5px 0 5px; 
    /* border: 1px solid #e6e5e2; */
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); 
 }
 .heart{
     display: flex;
     flex-direction: row;
     justify-content:flex-end;
    

 }
 .heart img{
     margin:7px;
     width: 20px;
     height: 19px;
     margin-right: 0;
     
 }
 .Itemimg{
    display: flex;
   justify-content: center;
  
 }
 .Itemimg img{
     margin-top:-25px;
     width: 100px;
 }
 
 .Itemname p{

    text-align: center;
    font-family: 'Rubik', sans-serif;
    font-size: 14px;
    margin-top:8px;
    margin-bottom:0;
    line-height: 1.1;
 }
 .Itemweight p{
    text-align: center;
    font-family: 'Rubik', sans-serif;
    font-size: 10px;
    line-height: 1.4;
    margin-bottom:0;

 }
 .Itemprice p{
    text-align: center;
    font-family: 'Rubik', sans-serif;
    font-size: 16px;
  
    margin-bottom:3px;
    margin-top: 7px;
 }
 .Itembutton{
    display: flex;
    justify-content: center;
 }
.ButtonAdd{
    text-align: center;
    font-family: 'Rubik', sans-serif;
    font-size: 13px;
  
    border-radius:6px;
    background-color:#3C9C23;
    color: white;
    width: 110px;
    height: 25px;
    border: none;
}
.BtnMoreLess{
 
    display: flex;
    justify-content: center;
    width: 110px;
    height: 25px;
}
.BtnMoreLess input{
    color: #4f4f4f;
    font-family: 'Rubik', sans-serif;
    width: 40px;
    border-style: solid;
    border-right: none;
    border-left: none;
    border-top-color: #3C9C23;
    border-bottom-color: #3C9C23;
    border-width: 1px;
    text-align: center;
    height: 25px;

    

}
.less, .more{
    background: #3C9C23;
    color:white;
    font-weight:bolder;
    text-align: center;
    width: 35px;
    height: 25px;
    border:solid 2px #3C9C23;
    
}
.less{
    border-bottom-left-radius: 5px;
    border-top-left-radius: 5px;
}
.more{
    border-bottom-right-radius: 5px;
    border-top-right-radius: 5px;
}

 .ItemList.row{
    align-items: center;
    justify-content: center;
 }

.menu-wrapper {
  overflow: hidden;
  height:100%;
  
}

.menu {
    height:120%;
    font-size: 18px ;
  list-style: none;
  margin: 5px 0 0 0;
  padding:0 5% 8px 5%;
  
  white-space: nowrap;
  overflow-x: scroll;
  overflow-y: hidden;
  


 
}

.menu li{
    display: table-cell;
    vertical-align: middle;
  

}
.menu a {
    display: block;
    text-decoration: none;
    color: rgb(20, 15, 15);
    font-size: 85%;
    margin-right:30px;
  }
  .cont{
    border-bottom:1px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
    /* border-bottom-style: groove */
  }

