#total-item{
   display: flex;
   justify-content: center;
   position: relative;
   width: 35px;
   height: 35px;
   border-radius: 50%;
   color:#3C9C23;
   background-color: white;
   margin:8px;
}
#total-item span{
   position: absolute;
   top:7px;
}
#canasta{
   display: flex;
   justify-content: center;
   margin-top:10px;
  
}
#canasta span{
   font-size: 20px; 
}
#total-price{
   margin-top:15px;
   margin-right:8px;
   display: flex;
   justify-content: center;
}
#canasta-total{
   display: flex;
   flex-direction: row;
   justify-content: space-between;

}

#navbartopfav p{
   margin-left:20vw;
   text-align: center;
   font-family: 'Rubik', sans-serif;
   font-size: 20px; 
   margin-top:8px;
}
#navbartopfav .row{
    height: 35px;
    margin-top:4px;
    
}
#navbartopfav .arrowb{
    margin-top:15px;
    margin-left: 5px;
    width: 14px;
    height:14px; 
}
.FavItem{
    width: 90vw;
    height: 32vw;
   
    margin: 2vw;
    border-radius: 5px; 
    /* border: 1px solid #e6e5e2; */
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); 
}

.FavItem .row{
    margin-top:14px;
}
.FavItemimg{
    margin-left:6vw;
    height: 80%;
    width: 30vw;
}
.FavItemimg img{
    width: 80%;
}
.FavItemname{
    width: 30vw;
    margin-right: 16vw;
}
.FavItemname p{
    font-size: 13px;
}
.FavItembutton{
    text-align: right;
   margin-top:-8vh;
   margin-right: 11px;
}
@media only screen and (max-device-width : 320px) {
    .FavItem{
    
        height: 33vw;
    
    
    }
    .FavItemimg img{
        width: 85%;
    }
    .FavItem .row{
        margin-top:4vw;
    }
}
@media only screen and (min-device-width : 321px) and (max-device-width : 375px) {
    .FavItem{
    
        height: 28vw;
    
    
    }
    .FavItemimg img{
        width: 70%;
    }
    .FavItem .row{
        margin-top:3vw;
        padding-bottom:1vw;
    }
}
@media only screen and (min-device-width : 376px) and (max-device-width : 425px) {
    .FavItem{
    
        height: 25vw;
    
    
    }
    .FavItemimg img{
        width: 65%;
    }
}
.FavoriteList{
    display: flex;
    justify-content: center;
}

html, body {
  height: 100%;
  margin: 0;
}
body{
  font-family: 'Rubik', sans-serif;
}
#root {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.content {
  flex: 1 1;
  overflow: auto;
 
}
